<template>
  <section id="work" class="work section">
    <div class="container section-intro">
      <h2 class="title is-2 is-uppercase">{{ title }}</h2>
      <p class="subtitle">
        {{ description }}
      </p>
    </div>
    <div class="container">
      <!-- check boxes -->
      <div id="projectSort">
        <div
          v-for="(category, index) in categories"
          :key="index"
          class="form-check form-check-inline"
        >
          <label
            class="form-check-label"
            :class="{ 'has-text-weight-semibold': category.checked }"
          >
            <input
              class="form-check-input"
              type="checkbox"
              v-model="category.checked"
              v-on:change="getfilteredData"
            />
            {{ category.value }}
          </label>
        </div>
      </div>

      <!-- end of checkboxes -->
      <div class="container">
        <div class="columns is-multiline is-6">
          <work-card
            v-for="(item, index) in filteredData"
            :key="index"
            :item="item"
          ></work-card>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import workCard from './elements/workCard';
import data from '../data/data';

export default {
  name: 'projects',
  props: ['description', 'title'],
  components: {
    'work-card': workCard,
  },
  computed: {
    selectedFilters() {
      const filters = [];

      const checkedFiters = this.categories.filter(obj => obj.checked);

      checkedFiters.forEach((element) => {
        filters.push(element.value);
      });

      return filters;
    },
  },

  data() {
    return {
      filteredData: [],
      categories: [
        {
          checked: false,
          value: 'Design',
        },

        {
          checked: false,
          value: 'Development',
        },

        {
          checked: false,
          value: 'Branding',
        },
      ],
    };
  },

  methods: {
    getfilteredData() {
      this.filteredData = data;
      let filteredDataByfilters = [];
      let filteredDataBySearch = [];

      // first check if filters where selected

      if (this.selectedFilters.length > 0) {
        filteredDataByfilters = this.filteredData.filter(obj =>
          this.selectedFilters.every(val => obj.category.indexOf(val) >= 0),
        );
        this.filteredData = filteredDataByfilters;
      }

      // then filter according to keyword, for now this only affects the name attribute of each data

      if (this.search !== '') {
        filteredDataBySearch = this.filteredData.filter(
          obj => obj.name.indexOf(this.search.toLowerCase()) >= 0,
        );
        this.filteredData = filteredDataBySearch;
      }
    },
  },

  mounted() {
    this.getfilteredData();
  },
};
</script>

